.work {
  width: 100%;
  padding: 150px 100px 0 100px;
  text-align: inline-start;
  position: relative;
  margin-top: 25vh;

  span {
    font-family: roboto-mono;
  }

  h3 {
    &::after {
      content: '';
      display: inline-block;
      width: 300px;
      height: 1px;
      margin: 5px 15px;
      background-color: transparentize($clr3, 0.7);
    }
  }

  a {
    width: 300px;
    height: 350px;
    background-image: url('../../assets/images/venyou.PNG');
    background-position: center;
    background-size: cover;
    border-radius: 3px;
    
    
    .proj-image {
      background-color: transparentize(darken($clr3, 20), 0.7);
      width: 100%;
      height: 100%;
      transition: all ease-in 0.3s;
      &:hover {
        background-color: transparentize(darken($clr3, 20), 1);
      }
    }
  }

  .start {
    flex-direction: row;
  }
  .end {
    flex-direction: row-reverse;
  }

  .proj {
    display: flex;
    margin-top: 50px;
    justify-content: space-around;
    
    .proj-info {

        display: flex;
        flex-direction: column;
        justify-content: space-evenly;

      p {
        background-color: lighten($clr1, 5);
        color: $clr4;
        border-radius: 3px;
        padding: 10px;
      }

      .proj-tech {
          display: flex;
          h4 {
              font-family: roboto-mono;
              padding: 0 20px;
          }
      }
    }
  }
}
